import React from "react";
import AdSense from 'react-adsense';

export const AdUnitType = {
    Grid: 0,
    InArticle: 1
};

export default function AdUnit({ type = AdUnitType.Grid }) {
    const publisherId = 'ca-pub-4889352773674825';

    if (type === AdUnitType.InArticle) {
        // adUnit = `
        // <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-4889352773674825" crossorigin="anonymous"></script>
        // <ins class="adsbygoogle"
        //     style="display:block; text-align:center;"
        //     data-ad-layout="in-article"
        //     data-ad-format="fluid"
        //     data-ad-client="ca-pub-4889352773674825"
        //     data-ad-slot="2581564797"></ins>
        // <script>
        //     (adsbygoogle = window.adsbygoogle || []).push({});
        // </script>`;
        return <AdSense.Google
            client={publisherId}
            slot='2581564797'
            style={{ display: 'block' }}
            layout='in-article'
            format='fluid'
        />;
    }

    // adUnit = `
    // <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-4889352773674825"
    //  crossorigin="anonymous"></script>
    // <!-- grid -->
    // <ins class="adsbygoogle"
    //     style="display:block"
    //     data-ad-client="ca-pub-4889352773674825"
    //     data-ad-slot="3654107349"
    //     data-ad-format="auto"
    //     data-full-width-responsive="true"></ins>
    // <script>
    //     (adsbygoogle = window.adsbygoogle || []).push({});
    // </script>
    // `;
    return <AdSense.Google
        client={publisherId}
        slot='3654107349'
        style={{ display: 'block' }}
        format='auto'
        responsive='true'
    />
}